.user-single {
    width: 960px;
    margin: 0 auto;
    @media screen and (max-width: 960px) {
        width: 100%;
        margin: 0 auto; } }
.headband {
    display: block;
    .banner {
        height: 30vh;
        position: relative;
        img {
            position: absolute;
            height: 30vh;
            width: 100%;
            overflow: hidden;
            border-radius: 10px;
            z-index: 1;
            opacity: 1; } }
    .no-image.banner {
        object-fit: cover !important;
        opacity: 1; }
    .profile {
        margin: 0vh 0em 1em 2em;
        position: relative;
        display: grid;
        grid-template-columns: 162px auto;
        z-index: 1;
        @media screen and (max-width: 960px) {
            margin: 0;
            grid-template-columns: auto; }
        .photo {
            @media screen and (max-width: 960px) {
                margin: auto; } }
        .photo img {
            width: 162px;
            height: 162px;
            border-radius: 300px;
            border: 7px solid var(--bg);
            margin-top: -9vh;
            margin-bottom: 0;
            @media screen and (max-width: 960px) {
                margin-top: -29vh;
                width: 132px !important;
                height: 132px; }
            @media screen and (max-width: 672px) {
                width: 120px !important;
                height: 120px; } }
        .no-img {
            background: var(--accent);
            padding: 2em; }
        .navbio {
            margin-left: 1.5rem;
            display: flex;
            align-items: center;
            @media screen and (max-width: 960px) {
                margin-left: 0rem;
                margin-bottom: 2em;
                display: block; }
            @media screen and (max-width: 672px) {
                margin-bottom: 1em; }
            .meta {
                margin-top: 1.25em;
                @media screen and (max-width: 960px) {
                    text-align: center; }
                h1 {
                    font-size: 1.2rem;
                    font-weight: 500; }
                p {
                    font-size: .98rem;
                    font-weight: 300;
                    padding-top: 0; } }
            .menu {
                display: flex;
                list-style: none;
                margin-left: auto;
                @media screen and (max-width: 960px) {
                    justify-content: center;
                    padding: 0; }
                li {
                    margin-left: 2em;
                    @media screen and (max-width: 960px) {
                        margin: 0 1em; } }
                li a {
                    border: var(--border-line);
                    color: var(--text);
                    padding: .9em 2em;
                    font-size: .8em;
                    border-radius: 7px;
                    font-weight: 500; } } } } }
.maindiv {
    display: grid;
    grid-template-columns: auto 30%;
    grid-gap: 3rem;
    @media screen and (max-width: 960px) {
        grid-template-columns: auto;
        grid-gap: 1rem; }
    .section {
        margin-bottom: 3rem;
        .consub {
            ul {
                padding-inline-start: 0;
                align-items: center; }
            li {
                font-size: .975rem;
                padding-top: 0;
                list-style: none;
                border-left: 1px solid orange;
                padding-left: 1em;
                align-item: center;
                text-transform: capitalize; } } }
    .headsub {
        font-size: 1.2rem;
        margin-bottom: .5em; }
    .section {
        p {
            font-size: .925rem; } }
    asside {
        .section {
            .consub {
                .connect {
                    @media screen and (max-width: 960px) {
                        display: grid;
                        grid-template-columns: 45% 45%;
                        grid-gap: 5%;
                        align-items: flex-start; }
                    @media screen and (max-width: 672px) {
                        display: grid;
                        grid-template-columns: auto; } }
                li {
                    border-left: var(--border-line);
                    padding-left: 0;
                    margin-top: 2em;
                    @media screen and (max-width: 960px) {
                        margin-top: 0em; }
                    @media screen and (max-width: 672px) {
                        margin-top: 1em; } } }
            .sub-title {
                display: flex;
                align-items: center;
                border-bottom: var(--border-line);
                flex-direction: row-reverse;
                img {
                    margin: 0; }
                span {
                    font-size: .925em;
                    font-weight: 500;
                    margin-right: auto;
                    padding-left: 1em; } }
            .meta {
                padding: 1em 0 1em 1em;
                display: flex;
                align-items: center;
                .value {
                    font-size: .875em;
                    text-transform: lowercase; }
                .action {
                    font-size: .8em;
                    margin-left: auto; } } } } }



